<template>
  <div class="question">
    <span class="title">
      <h3>Pergunta: <code>{{ ques.question }}</code></h3>
    </span>
    <span class="answer">
      <h4>Resposta: <strong>{{ ques.answers[0] }}</strong></h4>
    </span>
    <span>
      <b-button variant="primary" @click="editQuestion()">Editar pergunta </b-button>
      &nbsp;
      <b-button variant="danger" @click="this.removeModal = true">Remover pergunta</b-button>
    </span>
  </div>  
  <b-modal id="remove-modal"
    v-model="removeModal"
    title="Confirmar"
    @ok="deleteQuestion()">
    <strong>Tem certeza que deseja remover essa pergunta?</strong>
  </b-modal>
  <b-modal id="edit-modal"
    v-model="editModal"
    title="Editar pergunta"
    @ok="updateQuestion()"
    @hidden="resetQuestion()">
    <b-container fluid>
      <b-form>
        <b-form-group 
          label="Enunciado / Pergunta"
          label-for="input-question">
            <b-form-textarea id="input-question" v-model="editQues.question" rows="6" required></b-form-textarea>
        </b-form-group>

        <b-form-group 
          label="Respostas"
          label-for="input-answer0">            
            <b-form-text>Resposta <b style="color:green">correta</b></b-form-text>  
            <b-form-input id="input-answer0" v-model="editQues.answers[0]" required></b-form-input>
            <br/>
            <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
            <b-form-input id="input-answer1" v-model="editQues.answers[1]" required></b-form-input>
            <br/>
            <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
            <b-form-input id="input-answer2" v-model="editQues.answers[2]"></b-form-input>
            <br/>
            <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
            <b-form-input id="input-answer3" v-model="editQues.answers[3]"></b-form-input>

        </b-form-group>
      </b-form>
  </b-container>
  </b-modal>

</template>

<script>
export default {
  name: 'QuestionBlock',
  props: {
    ques: Object
  },
  data() {
    return {
      removeModal: false,
      editModal: false,
      editQues: this.copyQuesProp(),
    }
  },
  emits: ['delete-question', 'update-question'],
  methods: {
    copyQuesProp() {
      let localObj = {};
      Object.assign(localObj, this.ques);      
      localObj.answers = {...this.ques.answers}
      return localObj;      
    },
    deleteQuestion() {     
      this.removeModal = false;
      this.$emit('delete-question', {id: this.$.vnode.key});
    },
    editQuestion() {
      this.editModal = true;
    },
    updateQuestion() {
      this.editModal = false;      
      let editedQues = {
        id: this.$.vnode.key,
        question: this.editQues
      };
      
      this.$emit('update-question', editedQues);      
    },
    resetQuestion() {
      this.editModal = false;
      this.editQues = this.copyQuesProp();
    }
  }
}
</script>

